#q {
    width: 100%;
    height: 100vh;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
}


.loader {
    display: block;
    height: 100vh;
    width: 100%;
    position: fixed;
    z-index: 666;
    background: black;
    animation: 4s ease-in-out both loader;
}
@keyframes loader {
    0% {background: rgb(0, 0, 71);}
    100% {background: brown;}
}
.loaderBg {
    display: block;
    height: 100vh;
    width: 100%;
    background: fixed center / cover no-repeat url('../img/loaderBkgCmp.png');
}
.loaderBgColor {
    display: block;
    position: absolute;
    top: 10vh;
    left: calc(50% - 450px);
    height: 400px;
    width: 900px;
    background: rgb(0, 136, 136);
}
.loaderBgImg {
    position: absolute;
    top: 10vh;
    left: calc(50% - 450px);
    display: block;
    height: 400px;
    width: 900px;
    background-color: black;
    background: center / cover no-repeat url('../img/Scheme.png');
}
.loaderBgRed {
    display: block;
    position: absolute;
    top: 10vh;
    left: calc(50% - 450px);
    height: 400px;
    background: brown;
    animation: 4s ease-in-out both loaderColor;
}
@keyframes loaderColor {
    0% {width: 150px;}
    100% {width: 900px;}
}
body {
    background-color: rgba(0, 0, 0, 0.162);
    overflow-x: hidden;
}
a,p {
    text-decoration: none;
    color: black;
    font-size: 20px;
}
section {
    display: block;
    margin: 0 auto;
    width: 100%;
}
.container {
    display: block;
    margin: 0 auto;
    width: 1200px;
}
.bg-container {
    display: flex;
    flex-direction: row;
    position: relative;
}
.bg-hcombs {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: fixed center / cover repeat url('../img/MainWallpaper1.png');
}
.bg-dark {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
}
.bgFrst {
    display: block; 
    width: 25%;
    height: 100vh;
    animation: 7s 2s ease-in reverse none infinite gradientRed;
}
.bgScnd {
    width: 25%;
    height: 100vh;
    animation: 7s 2.1s ease-in reverse none infinite gradientRed;
}
.bgFrd {
    width: 25%;
    height: 100vh;
    animation: 7s 2.2s ease-in reverse none infinite gradientRed;
}
.bgFft {
    width: 25%;
    height: 100vh;
    animation: 7s 2.3s ease-in reverse none infinite gradientRed;
}
@keyframes gradientBlue {
    0% {background: blue;}
    25% {background: cornflowerblue;}
    50% {background: cyan;}
    75% {background: violet;}
    100% {background: blue;}
}
@keyframes gradientRed {
    9% {background: red;}
    18% {background: purple;}
    27% {background: blueviolet;}
    36% {background: blue;}
    45% {background: cornflowerblue;}
    54% {background: cyan;}
    63% {background: violet;}
    72% {background: blueviolet;}
    81% {background: purple;}
    90% {background: rgb(159, 0, 0);}
    99% {background: red;}
}

.menuLogoOpen {
    display: none;
    width: 50px;
    height: 50px;
    background: center no-repeat / 70% url(../img/menuWh.png);
    cursor: pointer;
    transition-duration: 0.2s;
    &:hover {
        transform: scale(1.05);;
        border: 1px solid gray;
        border-radius: 5px;
    }
}
header {
    width: 100%;
}
.mainMenu {
    position: fixed;
    width: 100%;
    height: 50px;
    // background: white;
    z-index: 9;
    // &:hover {
    //     background: rgba(255, 255, 255, 0.7);
    // }
