.mainMenuIcon {
    display: block;
    width: 40px;
    height: 40px;
    background: center no-repeat / contain url(../img/logo-icon-compr.png);
    cursor: pointer;
    transition-duration: 0.2s;
    &:hover {
        transform: scale(1.05);;
        border: 1px solid gray;
        border-radius: 50%;
    }
}
.translate {
    display: block;
    width: 40px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    cursor: pointer;
    transition-duration: 0.2s;
    color: white;
    &:hover {
        transform: scale(1.05);;
        border: 1px solid gray;
        border-radius: 50%;
    }
}
.mainNumber,
.mainMail {
    transition-duration: 0.2s;
    line-height: 40px;
    text-align: center;
    color: white;
    &:hover {
        transform: scale(1.05);;
        border: 1px solid gray;
        border-radius: 5px;
    }
}




