.mobileMenuBG {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 777;
    background: rgba(0, 0, 0, 0.777);
    animation: 0.3s ease-in none mobileShowMenu;
}
.mobileMenuClose {
    display: block;
    background: center no-repeat / cover url(../img/close.png);
    width: 50px;
    height: 50px;
    position: absolute;
    top: 20%;
    left: 80%;
    animation: 1s 0s ease-in none mobileShowItems;
}
@keyframes mobileShowMenu {
    0% {transform: translateX(-1500px);}
    100% {transform: translateX(0);}
}
@keyframes mobileShowItems {
    0% {transform: translateX(-1500px);}
    100% {transform: translateX(0);}
}
@keyframes mobileCloseItems {
    0% {transform: translateX(0);}
    100% {transform: translateX(-1500px);}
}
.mobileMenuItem {
    display: block;
    width: 100%;
    text-align: start;
    padding-left: 30px;
    color: white;
    font-size: 25px;
    margin-bottom: 10px;
    &:nth-of-type(1) {
        animation: 1s 0s ease-in none mobileShowItems;
    }
    &:nth-of-type(2) {
        animation: 1.3s 0s ease-in none mobileShowItems;
    }
    &:nth-of-type(3) {
        animation: 1.6s 0s ease-in none mobileShowItems;
    }
    &:nth-of-type(4) {
        animation: 1.9s 0s ease-in none mobileShowItems;
    }
    &:nth-of-type(5) {
        animation: 2.1s 0s ease-in none mobileShowItems;
    }
    &:nth-of-type(6) {
        animation: 2.5s 0s ease-in none mobileShowItems;
        margin-top: 20px;
    }
}
.mobileMenuItemClose {
    display: block;
    width: 100%;
    text-align: start;
    padding-left: 30px;
    color: white;
    font-size: 25px;
    margin-bottom: 10px;
    &:nth-of-type(1) {
        animation: 1s 0s ease-in none mobileCloseItems;
    }
    &:nth-of-type(2) {
        animation: 1.3s 0s ease-in none mobileCloseItems;
    }
    &:nth-of-type(3) {
        animation: 1.6s 0s ease-in none mobileCloseItems;
    }
    &:nth-of-type(4) {
        animation: 1.9s 0s ease-in none mobileCloseItems;
    }
    &:nth-of-type(5) {
        animation: 2.1s 0s ease-in none mobileCloseItems;
    }
    &:nth-of-type(6) {
        animation: 2.5s 0s ease-in none mobileCloseItems;
        margin-top: 20px;
    }
}