@media screen and (max-width: 1200px) {
    .container {
        width: 90%;
    }
}
@media screen and (max-width: 1000px) {
    .container {
        width: calc(100% - 50px);
    }
}
@media screen and (max-width: 950px) {
    .vlnb-discr,
    .pentest-discr {
        p {
            font-size: 16px;
        }
        a {
            height: 36px;
            font-size: 18px;
            line-height: 36px;
        }
    }
    .comercial {
        height: 210px;
        .container {
            p {
                font-size: 17px;
            }
            span {
                font-size: 12px;
            }
        }
    }
    .mainMenuPrtLeft {
        flex-direction: row;
        width: 180px;
        p {
            width: auto;
        }
    }
    .text-call-back {
        font-size: 20px;
    }
    footer h4 {
        font-size: 20px;
    }
    footer {
        a,p {
            font-size: 16px;
        }
        .mainMail, .mainNumber {
            text-align: start;
        }
    }
        
}
@media screen and (max-width: 900px) {
    .loaderBgColor {
        top: 30vh;
        left: calc(50% - calc(450px * 0.8));
        height: calc(400px * 0.8);
        width: calc(900px * 0.8);
        background: rgb(0, 136, 136);
    }
    .loaderBgImg {
        top: 30vh;
        left: calc(50% - calc(450px * 0.8));
        height: calc(400px * 0.8);
        width: calc(900px * 0.8);
    }
    .loaderBgRed {
        top: 30vh;
        left: calc(50% - calc(450px * 0.8));
        height: calc(400px * 0.8);
    }
    @keyframes loaderColor {
        0% {width: calc(150px * 0.8);}
        100% {width: calc(900px * 0.8);}
    }   
    .navi {
        margin: 100px 0 0 0;
    }
    h1 {
        text-align: center;
        width: 90%;
        padding: 0 0 10px 0;
        margin: 30px auto 0;
        border-left: none;
        border-bottom: 2px solid cyan;
        font-size: 28px;
    }
    h2 {
        text-align: center;
        margin: 40px auto;
        padding: 0;
        width: 90%;
        font-size: 20px;
    }
    .law {
        display: block;
        text-align: center;
        margin: 30px 0;
    }
    .mainMenu {
        a,p {
            font-size: 18px;
        }
    }
    .menuLogoOpen {
        width: 40px;
        height: 40px;
    }
    .mainMenuPrtRight {
        width: 450px;
    }
    .mainMenuPrtLeft {
        flex-direction: row;
        width: 160px;
    }


    .comercial {
        height: auto;
        .container {
            flex-direction: column;
        }
    }
}



@media screen and (max-width: 750px) {
    .bg-hcombs {
        background: url(../img/MainWallpaper1.png) center / contain fixed;
    }
    .mainMenuPrtLeft {
        width: 20%;
    }
    .mainMenuPrtRight {
        width: 60%;
    }
    .loaderBgColor {
        left: calc(50% - calc(450px * 0.7));
        height: calc(400px * 0.7);
        width: calc(900px * 0.7);
        background: rgb(0, 136, 136);
    }
    .loaderBgImg {
        left: calc(50% - calc(450px * 0.7));
        height: calc(400px * 0.7);
        width: calc(900px * 0.7);
    }
    .loaderBgRed {
        left: calc(50% - calc(450px * 0.7));
        height: calc(400px * 0.7);
    }
    @keyframes loaderColor {
        0% {width: calc(150px * 0.7);}
        100% {width: calc(900px * 0.7);}
    }   
    h1 {
        text-align: center;
        width: 90%;
        padding: 0 0 10px 0;
        border-left: none;
        border-bottom: 2px solid cyan;
        font-size: 28px;
    }
    h2 {
        text-align: center;
        margin: 30px auto 0;
        padding: 0;
        width: 90%;
        font-size: 20px;
    }
    .mainMenu {
        a,p {
            font-size: 18px;
        }
    }
    .text-call-back {
        padding: 0;
    }
    footer {
        h4 {
            font-size: 20px;
        }
        p,a {
            font-size: 17px;
        }
    }
}


@media screen and (max-width: 700px) {
    .mainMenuPrtRight {
        width: 70%;
    }
    .loaderBg {
        background: fixed 0% 0% / 400% no-repeat url('../img/loaderBkgCmp.png');
    }
    .about-vlnb,
    .about-pentest {
        flex-direction: column;
    }
    .vlnb-image,
    .vlnb-discr,
    .pentest-image,
    .pentest-discr {
        justify-content: center;
        align-items: center;
        width: 100%;
        h3,a,p {
            text-align: center;
        }
    }
    .personal-data {
        font-size: 12px;
    }

    .service-pentest-img-rd,
    .service-scan-img-rd {
        width: 100%;
        height: 300px;
    }
    .service-pentest-dsc-rd,
    .service-scan-dsc-rd {
        flex-direction: column-reverse;
        p {
            width: calc(100% - 10px);
            margin: 0 auto;
        }
    }
    .service-scan-img-bl,
    .service-pentest-img-bl {
        display: none;
    }
    .service-pentest-dsc-bl,
    .service-scan-dsc-bl {
        flex-direction: column;
        p {
            width: calc(100% - 10px);
            margin: 0 auto;
        }
    }

}
@media screen and (max-width: 600px) {
    .menuLogoOpen {
        display: block;
    }
    .loaderBgColor {
        left: calc(50% - calc(450px * 0.6));
        height: calc(400px * 0.6);
        width: calc(900px * 0.6);
        background: rgb(0, 136, 136);
    }
    .loaderBgImg {
        left: calc(50% - calc(450px * 0.6));
        height: calc(400px * 0.6);
        width: calc(900px * 0.6);
    }
    .loaderBgRed {
        left: calc(50% - calc(450px * 0.6));
        height: calc(400px * 0.6);
    }
    @keyframes loaderColor {
        0% {width: calc(150px * 0.6);}
        100% {width: calc(900px * 0.6);}
    }  
    .mainMenuPrtRight {
        display: none;
    }
    .mainMenuPrtLeft {
        width: calc(50% + 25px);
        flex-direction: row-reverse;
        justify-content: space-between;
    }


    .service-pentest-dsc-rd, .service-scan-dsc-rd, .service-pentest-dsc-bl, .service-scan-dsc-bl {
        p,span {font-size: 15px;}
    }
    .service-pentest-img-rd, .service-scan-img-rd {
        height: 200px;
    }
}


